// src/components/exercise-common/CategorySelector/index.js
import { useState, useCallback, memo, useEffect, useRef, useMemo } from "react";
import { View, ScrollView, TouchableOpacity, Text, Animated } from "react-native";
import { LinearGradient } from "expo-linear-gradient";
import PropTypes from 'prop-types';
import createStyles from "./style";

// Réfrence pour les mesures de layout du ScrollView
const itemLayouts = {};

const CategorySelector = ({
  categories = [],
  selectedCategory,
  onSelectCategory,
  primaryColor = "#5E60CE",
}) => {
  const styles = createStyles(primaryColor);
  const scrollViewRef = useRef(null);
  const isAnimatingRef = useRef(false);

  // Initialisation de l'objet de référence des animations
  const animationsRef = useRef({
    values: {},
    press: {},
  });

  // Gérer les animations de sélection
  const animateSelection = useCallback((newCategoryId) => {
    if (isAnimatingRef.current) return;
    isAnimatingRef.current = true;

    const animations = [];
    const currentCategoryAnim = animationsRef.current.values[selectedCategory];
    const newCategoryAnim = animationsRef.current.values[newCategoryId];

    if (currentCategoryAnim) {
      animations.push(
        Animated.timing(currentCategoryAnim, {
          toValue: 0,
          duration: 200,
          useNativeDriver: false,
        })
      );
    }

    if (newCategoryAnim) {
      animations.push(
        Animated.timing(newCategoryAnim, {
          toValue: 1,
          duration: 300,
          useNativeDriver: false,
        })
      );
    }

    if (animations.length > 0) {
      Animated.parallel(animations).start(() => {
        isAnimatingRef.current = false;
      });
    } else {
      isAnimatingRef.current = false;
    }
  }, [selectedCategory]);


  // Synchroniser les instances d'animation avec la liste de catégories
  useEffect(() => {
    const currentAnimations = animationsRef.current.values;
    const newAnimations = {};

    categories.forEach(category => {
      if (currentAnimations[category.id]) {
        newAnimations[category.id] = currentAnimations[category.id];
      } else {
        newAnimations[category.id] = new Animated.Value(
          category.id === selectedCategory ? 1 : 0
        );
      }
    });

    animationsRef.current.values = newAnimations;
  }, [categories, selectedCategory]);

  // Déplacer la logique de défilement vers un effet séparé
  useEffect(() => {
    if (scrollViewRef.current && selectedCategory) {
      const layout = itemLayouts[selectedCategory];
      if (layout) {
        scrollViewRef.current.scrollTo({
          x: layout.x - (layout.width / 2),
          y: 0,
          animated: true,
        });
      }
    }
  }, [selectedCategory, categories]);

  const handlePressIn = useCallback((categoryId) => {
    if (!animationsRef.current.press[categoryId]) {
      animationsRef.current.press[categoryId] = new Animated.Value(1);
    }
    Animated.timing(animationsRef.current.press[categoryId], {
      toValue: 0.95,
      duration: 150,
      useNativeDriver: true,
    }).start();
  }, []);


  const handlePressOut = useCallback((categoryId) => {
    Animated.timing(animationsRef.current.press[categoryId], {
      toValue: 1,
      duration: 150,
      useNativeDriver: true,
    }).start();
  }, []);


  const handleCategoryPress = useCallback((categoryId) => {
    if (categoryId !== selectedCategory && !isAnimatingRef.current) {
      onSelectCategory(categoryId);
      animateSelection(categoryId);
    }
  }, [selectedCategory, onSelectCategory, animateSelection]);


  const categoryPills = useMemo(() => {
    return categories.map((category) => {
      const isSelected = selectedCategory === category.id;
      
      // Récupérer les animations existantes, en fournissant une valeur par défaut
      const animation = animationsRef.current.values[category.id] || new Animated.Value(0);
      const pressAnimation = animationsRef.current.press[category.id] || new Animated.Value(1);


      const scale = animation.interpolate({
        inputRange: [0, 1],
        outputRange: [1, 1.05],
      });


      const pressScale = pressAnimation.interpolate({
        inputRange: [0.95, 1],
        outputRange: [0.95, 1],
      });


      const shadowOpacity = animation.interpolate({
        inputRange: [0, 1],
        outputRange: [0.1, 0.25],
      });


      const borderWidth = animation.interpolate({
        inputRange: [0, 1],
        outputRange: [1, 2],
      });


      return (
        <Animated.View
          key={category.id}
          onLayout={(event) => {
            itemLayouts[category.id] = event.nativeEvent.layout;
          }}
          style={[
            styles.categoryItemWrapper,
            {
              transform: [{ scale: Animated.multiply(scale, pressScale) }],
            },
          ]}
        >
          <TouchableOpacity
            style={styles.categoryTouchable}
            onPress={() => handleCategoryPress(category.id)}
            onPressIn={() => handlePressIn(category.id)}
            onPressOut={() => handlePressOut(category.id)}
            activeOpacity={0.8}
            accessibilityRole="button"
            accessibilityLabel={String(category.name)}
            accessibilityState={{ selected: isSelected }}
          >
            {isSelected ? (
              <LinearGradient
                colors={[primaryColor, `${primaryColor}E6`, `${primaryColor}CC`]}
                start={{ x: 0, y: 0 }}
                end={{ x: 1, y: 1 }}
                style={styles.selectedCategoryItem}
              >
                <View style={styles.selectedInner}>
                  <Text style={styles.selectedCategoryText}>
                    {category.name}
                  </Text>
                  <View style={styles.sparkleContainer}>
                    <Text style={styles.sparkle}>✨</Text>
                  </View>
                </View>
              </LinearGradient>
            ) : (
              <Animated.View
                style={[
                  styles.categoryItem,
                  {
                    borderWidth,
                    borderColor: `${primaryColor}20`,
                    shadowOpacity,
                  }
                ]}
              >
                <View style={[styles.categoryInner, { backgroundColor: `${primaryColor}08` }]}>
                  <Text style={[styles.categoryText, { color: primaryColor }]}>
                    {category.name}
                  </Text>
                </View>
              </Animated.View>
            )}
          </TouchableOpacity>
        </Animated.View>
      );
    });
  }, [categories, selectedCategory, primaryColor, handlePressIn, handlePressOut, styles]);

  return (
    <View style={styles.container}>
      <LinearGradient
        colors={[`${primaryColor}04`, 'transparent', `${primaryColor}02`]}
        start={{ x: 0, y: 0 }}
        end={{ x: 1, y: 0 }}
        style={styles.backgroundGradient}
      >
        <ScrollView
          ref={scrollViewRef}
          horizontal
          showsHorizontalScrollIndicator={false}
          contentContainerStyle={styles.scrollContent}
          style={styles.scrollView}
        >
          {categoryPills}
        </ScrollView>
      </LinearGradient>
    </View>
  );
};

CategorySelector.propTypes = {
  categories: PropTypes.arrayOf(PropTypes.shape({
    id: PropTypes.oneOfType([PropTypes.string, PropTypes.number]).isRequired,
    name: PropTypes.string.isRequired,
  })).isRequired,
  selectedCategory: PropTypes.oneOfType([PropTypes.string, PropTypes.number]),
  onSelectCategory: PropTypes.func.isRequired,
  primaryColor: PropTypes.string,
};


export function areEqual(prevProps, nextProps) {
  const categoriesEqual = prevProps.categories === nextProps.categories ||
    (prevProps.categories.length === nextProps.categories.length &&
      prevProps.categories.every((cat, index) => cat.id === nextProps.categories[index].id));

  return (
    prevProps.selectedCategory === nextProps.selectedCategory &&
    prevProps.primaryColor === nextProps.primaryColor &&
    categoriesEqual
  );
}

export default memo(CategorySelector, areEqual);